import * as React from "react"
import { initResources } from "./../resources/ImageManager"
import HomePage from "./HomePage"

const LOADING_STYLE = {
    margin: "0",
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    fontSize: "20pxcs",
    backgroundColor: "0x101010"
}

interface LoadingPageState
{
    loaded: boolean
    dotCount: number
}

export default class LoadingPage extends React.Component<{}, LoadingPageState>
{
    constructor(props: object)
    {
        super(props)
        this.state = { loaded: false, dotCount: 0 }
    }

    changeDotCount()
    {
        this.setState({ dotCount: (this.state.dotCount + 1) % 3 })
        setTimeout(this.changeDotCount.bind(this), 1000)
    }

    componentDidMount()
    {
        initResources(this.setState.bind({ loaded: true }))
        setTimeout(this.changeDotCount.bind(this), 1000)
    }

    render()
    {
        const loadingView = <div style={LOADING_STYLE}>Loading{".".repeat(this.state.dotCount)}</div>
        const pageView = <HomePage />
        return (this.state.loaded ? pageView : loadingView)
    }
}